.wrapper {
    position: relative;
    border-radius: 24px;
}

.header {
    padding: 32px 48px 0 32px;
    margin-bottom: 16px;
}

.footer {
    padding: 0 0 32px 32px;
    margin-top: 68px;
}

.mobileFooter {
    padding: 0 32px 32px 32px;
    margin-top: 68px;
}

.highlightArrow {
    position: absolute;
    border: 16px solid var(--gray);
    border-radius: 50%;
    bottom: -16px;
    right: -16px;
}

.is-responsive-height {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.is-full-width {
    width: 100%;
}

.is-full-height {
    height: 100%;
}

.arrow {
    margin-top: 25px;
    margin-right: -10px;
    border: 1px solid black;
    border-radius: 50%;
    position: absolute;
    top: 15px;
    right: 0;
}